@use 'src/app/shared/style/fonts.scss' as *;
@use 'src/app/shared/style/variables.scss' as *;
@use 'src/app/shared/style/global.scss' as *;
@use 'src/app/shared/style/global.scss' as *;
@use 'src/app/shared/style/scrollbars.scss' as *;

html {
	font-size: 62.5%;
	scroll-behavior: smooth;
}

@media (max-width: 768px) {
	html {
		font-size: 48.5%;
	}
	.desktop {
		display: none;
	}
}
@media (min-width: 768px) {
	.mobile {
		display: none;
	}
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
		'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	font-size: 1.6rem;
	font-family: 'Nunito', sans-serif;
	color: $light1;

	* {
		margin: 0;
		padding: 0;
		outline: none;
		box-sizing: border-box;
	}
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1 {
	font-size: 6.2rem;
	font-weight: 700;
	font-family: $font2;
}

h2 {
	font-size: 4rem;
	font-weight: 600;
}

h3 {
	font-size: 2.2rem;
	font-weight: 300;
}

p {
	font-size: 1.6rem;
	font-weight: 400;
	&.l {
		font-size: 1.8rem;
	}
	&.xl {
		font-size: 2rem;
	}
}

a:link {
	color: $light0;
	text-decoration: none;
}

/* visited link */
a:visited {
	color: $light0;
}

/* mouse over link */
a:hover {
	color: $accent0;
}

/* selected link */
a:active {
	color: $accent0;
}
